section.inner_content div.column_header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
}

section.inner_content div.selector {
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  align-content: center;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 30px;
}
section.inner_content div.selector h3 {
  /* display: inline-flex; */
  align-content: center;
  align-items: center;
  justify-content: center;
  font-size: 1em;
  padding: 4px 20px;
  margin-bottom: 0;
  white-space: nowrap;
}
section.inner_content div.column_header h2 {
  margin-right: 20px;
  white-space: nowrap;
  font-size: 20px;
  font-stretch: revert;
  font-weight: bold;
}

section.inner_content.no_pad div.column_header {
  padding-left: 40px;
  padding-right: 40px;
}
.column_header {
  display: flex;
  flex-direction: row;
}
section.inner_content div.selected h3 a {
	/* background: linear-gradient(to right, rgba(var(--tmdbLighterGreen), 1.0) 0%, rgba(var(--tmdbLightGreen), 1.0) 100%); */
	background: white;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}
